@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');
*{ box-sizing: border-box; }
html, body {
  margin: 0;
}
body {
  font-family: 'Inter', sans-serif;
  background-color: aliceblue;
}
img {
  display: block;
  max-width: 100%;
}

.App-header {
  background-color: chocolate;
  text-align: center;
  padding: 1.5em;
  color: aliceblue;
}
.App-body {
  display: flex;
  flex-direction: column;
  gap: 2em;
  max-width: 900px;
  margin: 0 auto;
  padding: 2em 1em; 
}
.App-card {
  display: grid;
  grid-template-areas: 
    'image' 
    'header'
    'body'
  ;
  gap: 1em;
  padding-bottom: 2em;
}
.App-card:not(:last-child) {
  border-bottom: 1px solid #dbdbdb;
}

.card_header {
  grid-area: header;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-areas: 
    'location link'
    'title title'
  ;
}
.card_header h2 {
  grid-area: title;
  font-size: 2.2rem;
  margin: 0;
  margin-top: .2em;
}
.card_header p {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: .17em;
  margin-right: .8em;
}
.card_header .location_icon {
  margin-right: .3em;
}
.card_header a {
  color: #918E9B;
  transition: all .2s linear;
  justify-self: end;
}
.card_header a:focus,
.card_header a:hover {
  color: black;
}

.card_body {
  grid-area: body;
}
.card_body .date {
  font-weight: 700;
}

.card_image {
  grid-area: image;
  border-radius: 5px;
  overflow: hidden;
}
.card_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (min-width:35em) {
  .App-card {
    grid-template-columns: auto 1fr;
    grid-template-areas: 
      'image header'
      'image body'
    ;
  }
  .card_image {
    max-width: 200px;
  }
}